
.nav-menu{
    display: flex;
}

.nav-menu li {
    padding : 0 1rem;
    list-style-type: none;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.header{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding : 1rem ;
    position: fixed;
    width:100%;
    height:50px;
    z-index: 10; 
    padding:2rem 30px;
}

.header-bg{
    background-color: rgba(19,19,19,0.8);
    transition: 0.5s;
}

.Hamburger{
    display: none;
}

@media screen and (max-width:1040px) {

   .nav-menu{
    display: flex ;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 100vh;
    background: rgba(0,0,0,0.9);
    position : absolute;
    top: 0;
    left:-100%;
    z-index:-3;
    transition: 0.3s;
   }

   .nav-menu.active{
    left:0%;
   }

   .nav-menu li{
    padding: 1rem 0;
   }

   .nav-menu li a {
    font-size: 2rem;
   }

   .Hamburger{
    display: initial;
}



}